import { Component, OnInit } from '@angular/core';
import { UtilProvider } from '../services/util-provider';
import { Router } from '@angular/router';
import { AppData } from '../services';

@Component({
  selector: 'app-add-money-wallet-cancel',
  templateUrl: './add-money-wallet-cancel.page.html',
  styleUrls: ['./add-money-wallet-cancel.page.scss'],
})
export class AddMoneyWalletCancelPage implements OnInit {

  constructor(private util: UtilProvider, private router: Router, public appData: AppData) { 
    this.appData.newTheme = this.util.getNewTheme();
  }

  ngOnInit() {
    this.util.showToast('Payment failed', 'bottom');
    setTimeout(() => {
      this.router.navigate(['/wallet']);
    }, 2000);
  }

}
